html,
body {
  height: 100%; }

body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Roboto", "Aria", serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #1f1e25; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

@media (max-width: 767px) {
  body {
    font-size: 14px;
    line-height: 16px; } }

.container {
  max-width: 1366px;
  margin: 0 auto; }

@media (max-width: 1023px) {
  .container {
    max-width: 1023px; } }

@media (max-width: 767px) {
  .container {
    max-width: 767px; } }

@media (max-width: 320px) {
  .container {
    max-width: 320px; } }

.header__wrapper {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  margin: 25px 98px 36px 61px; }

.header__logo {
  display: inline-block;
  vertical-align: middle;
  margin-right: 56px; }

.header__nav-toggle {
  display: none; }

@media (max-width: 1064px) {
  .header__wrapper {
    align-items: flex-start;
    margin: 23px 45px 19px 40px; } }

@media (max-width: 1023px) {
  .header__logo {
    margin-right: 63px; }
    .header__logo svg {
      width: 140px;
      height: 24px; } }

@media (max-width: 767px) {
  .header__wrapper {
    flex-wrap: wrap;
    margin: 12px 15px 14px 15px; }
  .header__logo {
    margin-left: 28px; }
    .header__logo svg {
      width: 107px;
      height: 18px; }
  .header__nav {
    order: 1;
    width: 100%; }
  .header__nav-toggle {
    position: fixed;
    top: 19px;
    left: 15px;
    display: inline-block;
    vertical-align: middle;
    width: 18px;
    height: 12px;
    margin: 0;
    padding: 0;
    background-color: transparent;
    border: none; }
    .header__nav-toggle_closed::before, .header__nav-toggle_opened::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 0;
      width: 100%;
      height: 100%;
      background-repeat: no-repeat;
      background-position: 50%;
      transform: translateY(-50%); }
    .header__nav-toggle_closed::before {
      background-image: url("../img/icon-menu-open.svg"); }
    .header__nav-toggle_opened::before {
      background-image: url("../img/icon-menu-close.svg"); }
  .nojs .header__logo {
    margin-left: 0; }
  .nojs .header__nav-toggle {
    display: none; } }

.nav-main {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none; }
  .nav-main li {
    max-width: 150px;
    margin-right: 25px;
    overflow: hidden;
    text-overflow: ellipsis; }
    .nav-main li:last-of-type {
      margin-right: 0; }
  .nav-main a {
    font-family: "Roboto", "Aria", serif;
    color: #1f1e25;
    text-decoration: none; }

@media (max-width: 1023px) {
  .nav-main li {
    margin-right: 20px; }
  .nav-main a {
    font-size: 14px;
    line-height: 16px; } }

@media (max-width: 767px) {
  .nav-main {
    position: fixed;
    top: 54px;
    display: flex;
    flex-direction: column;
    width: 95%;
    height: 100vh; }
    .nav-main_closed {
      left: -95%;
      transition: 0.5s ease; }
    .nav-main_opened {
      left: 0;
      background-color: #f6f7ff;
      transition: 0.5s ease; }
    .nav-main li {
      width: 100%;
      max-width: 280px;
      margin: 0;
      padding: 20px 0;
      text-align: center; }
  .nojs .nav-main {
    position: static; } }

.nav-user {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 0;
  margin-left: auto;
  padding: 0;
  list-style: none; }
  .nav-user li {
    max-width: 150px;
    margin-right: 25px;
    overflow: hidden;
    text-overflow: ellipsis; }
    .nav-user li:last-of-type {
      margin-right: 0; }
  .nav-user a {
    display: flex;
    flex-direction: row;
    align-items: center;
    font-family: "Roboto", "Aria", serif;
    color: #1f1e25;
    text-decoration: none; }
  .nav-user span {
    margin-left: 10px; }

@media (max-width: 1023px) {
  .nav-user span {
    display: none; } }

@media (max-width: 767px) {
  .nav-user svg {
    width: 15px;
    height: 22px; } }
